	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Это базовое, эффективное упражнение, цель которого проработка трицепсов, средней части грудных мышц и передней головки дельтовидной мышцы. стать причиной микротравм сухожилий и болезненных ощущений в локтях.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>В качестве инвентаря для данного упражнения используется горизонтальная скамья.</li>\n  <li>Исходное положение: лягте на горизонтальную скамью. Возьмите штангу таким образом, чтобы ширина хвата была уже ширины ваших плеч. Удерживая снаряд над грудью, выведите его чуть ниже уровня вашей груди.</li>\n  <li>На вдохе – плавно опускайте гриф до тех пор, пока не гриф не коснется вашей груди. Следует обратить внимание, что гриф должен находиться немного ниже уровня груди, а руки ходят вдоль туловища.</li>\n  <li>На выдохе – используя силу трицепсов, плавно выпрямите руки, вернув штангу к исходному положению. В верхней точке упражнения зафиксируйте штангу на несколько секунд.</li>\n  <li>Сделайте необходимое количество повторений.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Расстояние между кистями должно быть приблизительно 20 – 30 см. Если хват будет уже, то локти будут непроизвольно расходиться в стороны.</li>\n  <li>Используется бинты для кистей, т.к. нагрузка на кисти в данном упражнении больше чем в классическом жиме.</li>\n   <li>Используя различные вариации узкого хвата, можно добиться трех различных результатов - развитие максимальной силы (одно-пять повторений, с большим отягощением); максимальной массы (восемь-дваенадцать повторений, полный диапазон); сочетание массы и силы (умеренная/высокая интенсивность, пять-восемь повторений).</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/kbf_MdonwSs\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/jim-shtangi-uzkim-hvatom","efficiency":"5","title":"Жим штанги узким хватом","image":"/img/4-07-0.png","image_2":"/4-07-1.png","image_3":"","image_4":"","filterby":"triceps","date":"April 12, 2017","injury":"5","bodypart":"Трицепс","filtertype":"base","type":"Базовое","filterinventory":"stange","inventory":"Штанга","filterplace":null}}},"pathContext":{}}

